import { Injectable } from '@angular/core';

import { Publication } from '../models/publication';
import { TodoItem } from '../models/todoitem';
import { Observable, of } from 'rxjs';

import { map, catchError } from 'rxjs/operators';
import { HttpClient, HttpHeaders } from '@angular/common/http';
import { baseURL } from '../shared/baseurl';

import { ProcessHTTPMsgService } from './process-httpmsg.service';

@Injectable({
  providedIn: 'root'
})
export class PublicationService {

  constructor(private http: HttpClient,
              private processHTTPMsgService: ProcessHTTPMsgService) { }

  getPublications(): Observable<Publication[]> {
    return this.http.get<Publication[]>('https://vitmfs.github.io/assets/publications_db.json'/*baseURL + 'Publications'*/)
      .pipe(catchError(this.processHTTPMsgService.handleError));
  }

  // https://localhost:44373/api/EmployeeGeofences

  getEmployeeGeofences(): Observable<object[]> {
    const employeeGeofencesUrlEndpoint = 'https://localhost:44373/api/EmployeeGeofences';
    return this.http.get<object[]>(employeeGeofencesUrlEndpoint)
      .pipe(catchError(this.processHTTPMsgService.handleError));
  }

  getRosterGeofences(): Observable<object[]> {
    const rosterGeofencesUrlEndpoint = 'https://localhost:44373/api/RosterGeofences';
    return this.http.get<object[]>(rosterGeofencesUrlEndpoint)
      .pipe(catchError(this.processHTTPMsgService.handleError));
  }

  getEmployeeGeofence(): Observable<object> {
    return this.http.get<object>('https://localhost/QualityOfLifePortalAPI/api/employeegeofences/getemployeegeofence?employeeCode=3&startDate=2012-06-21%2010%3A34%3A09.000&geofenceID=3')
      .pipe(catchError(this.processHTTPMsgService.handleError));
  }

  getRosterGeofence(): Observable<object> {
    return this.http.get<object>('https://localhost/QualityOfLifePortalAPI/api/rostergeofences/getrostergeofence?rosterCode=2&startDate=2012-06-20%2010%3A34%3A09.000&geofenceID=2')
      .pipe(catchError(this.processHTTPMsgService.handleError));
  }

  postEmployeeGeofence(employeeGeofenceToPost: object): Observable<object> {
    const httpOptions = {
      headers: new HttpHeaders({
        'Content-Type':  'application/json'
      })
    };

    const body: string = JSON.stringify({
      "employeeCode": 26,
      "startDate": "2021-07-22T15:22:47.128Z",
      "endDate": "2021-07-22T15:22:47.128Z",
      "geofenceID": 26,
      "timeStamp": "2021-07-22T15:22:47.128Z"
    });
    // alert(body);

    return this.http.post<Publication>('https://localhost/QualityOfLifePortalAPI/api/employeegeofences/save', body, httpOptions)
      .pipe(catchError(this.processHTTPMsgService.handleError));

  }

  deleteEmployeeGeofence(employeeGeofenceToPost: object): Observable<object> {
    const httpOptions = {
      headers: new HttpHeaders({
        'Content-Type':  'application/json'
      })
    };

    const body: string = JSON.stringify({
      "employeeCode": 25,
      "startDate": "2021-07-22T15:22:47.128Z",
      "endDate": "2021-07-22T15:22:47.128Z",
      "geofenceID": 25,
      "timeStamp": "2021-07-22T15:22:47.128Z"
    });
    // alert(body);

    return this.http.post<Publication>('https://localhost/QualityOfLifePortalAPI/api/employeegeofences/delete?employeeCode=25&startDate=2021-07-22%2015%3A22%3A47.127&geofenceID=25', body, httpOptions)
      .pipe(catchError(this.processHTTPMsgService.handleError));

  }

  postRosterGeofence(rosterGeofenceToPost: object): Observable<object> {
    const httpOptions = {
      headers: new HttpHeaders({
        'Content-Type':  'application/json'
      })
    };

    const body: string = JSON.stringify({
      "RosterCode": 27,
      "StartDate": "2021-07-23T11:10:08.181Z",
      "EndDate": "2021-07-23T11:10:08.181Z",
      "GeofenceID": 27,
      "TimeStamp": "2021-07-23T11:10:08.181Z",
      "Timestamp": "2021-07-23T11:10:08.181Z"
    });
    // alert(body);

    return this.http.post<Publication>('https://localhost/QualityOfLifePortalAPI/api/rostergeofences/save', body, httpOptions)
      .pipe(catchError(this.processHTTPMsgService.handleError));

  }

  deleteRosterGeofence(rosterGeofenceToPost: object): Observable<object> {
    const httpOptions = {
      headers: new HttpHeaders({
        'Content-Type':  'application/json'
      })
    };

    const body: string = JSON.stringify({
      "RosterCode": 10,
      "StartDate": "2021-07-23T11:10:08.181Z",
      "EndDate": "2021-07-23T11:10:08.181Z",
      "GeofenceID": 10,
      "TimeStamp": "2021-07-23T11:10:08.181Z",
      "Timestamp": "2021-07-23T11:10:08.181Z"
    });
    // alert(body);

    return this.http.post<Publication>('https://localhost/QualityOfLifePortalAPI/api/rostergeofences/delete?rosterCode=10&startDate=2021-07-23%2011%3A10%3A08.180&geofenceID=10', body, httpOptions)
      .pipe(catchError(this.processHTTPMsgService.handleError));

  }

  // getTodoItems(): Observable<TodoItem[]> {
  //   return this.http.get<TodoItem[]>('https://localhost:44304/api/TodoItems')
  //     .pipe(catchError(this.processHTTPMsgService.handleError));
  // }


  postPublication(publicationToPost: Publication): Observable<Publication> {
    const httpOptions = {
      headers: new HttpHeaders({
        'Content-Type':  'application/json'
      })
    };

    const body: string = JSON.stringify(publicationToPost);
    // alert(body);

    return this.http.post<Publication>(baseURL + 'publications/', body, httpOptions)
      .pipe(catchError(this.processHTTPMsgService.handleError));

  }
  postTodoItem(todoItemToPost: TodoItem): Observable<TodoItem> {
    const httpOptions = {
      headers: new HttpHeaders({
        'Content-Type':  'application/json'
      })
    };

    const body: string = JSON.stringify(todoItemToPost);
    // alert(body);

    return this.http.post<TodoItem>(baseURL + 'new_publications/', body, httpOptions)
      .pipe(catchError(this.processHTTPMsgService.handleError));

  }

  putPublication(publicationToPut: Publication): Observable<Publication> {
    const httpOptions = {
      headers: new HttpHeaders({
        'Content-Type':  'application/json'
      })
    };
    const body: string = JSON.stringify(publicationToPut);
    alert(body);
    return this.http.put<Publication>(baseURL + 'publications/13', body, httpOptions)
      .pipe(catchError(this.processHTTPMsgService.handleError));
  }
}


